* {
  box-sizing: border-box;
}

:root {
  --color-accent: #00acdc;
}

html {
  color: #000;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.logo-img {
  margin: 10px 0;
  display: block;
}

.nav-list {
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav-item:not(:first-child) {
  margin-left: 20px;
}

.nav-link.is-active, .nav-link:hover {
  color: var(--color-accent);
}

.is-active:after {
  content: "";
  background-color: var(--color-accent);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

